body{
    background-color:#0f0f0f;
}
.box{
    margin:9%;
    display:flex;
    justify-content: center;
    align-items: center;
}
.search{
    border:none;
   width: 15cm;
   border-radius: 20px;
   background-color:rgb(43, 42, 42);
   padding:.3em;
}
.search input{
    width:13cm;
    background-color:rgb(43, 42, 42);
    border:none;
    outline:none;
    height:2em;
    position: relative;
    left:1em;
    border-radius: 5px;
    color:rgb(168, 172, 172);
    font-size: 1em;
}
.box h2{
    text-align: center;
    color:white;
    font-size:4em;
font-family: 'Raleway', sans-serif;
}
hr{
    border-color: rgb(55, 54, 54);
    width:90%;
}
.box2{
    display:flex;
}
#searchi {
    position: relative;
    top:.5em;
    left:.5em;
}
.flex{
    display:flex;
    justify-content: center;
    color:rgb(240, 235, 235);
}
.flex1{
    display:flex;
    gap:3em;
}
h3{
    border:none;
    border-radius: 10px;
    background-color: #333131;
    padding:.5em
}
h3:hover{
    border:1px solid white;
}