.delete{
    background-color:rgb(56, 54, 54);
    border:none;
    position:absolute;
    left:63%;
    color:rgb(136, 136, 141);
    display:none;
    font-size:1em;
}
.recent{
    padding-left: 2em;
    border-radius: 10px;
}
.recent:hover .delete{
    display:inline;
    color:rgb(120, 120, 234);
    background-color: rgb(63, 62, 62);
    text-decoration:underline;
}
.recent:hover{
    background-color: rgb(63, 62, 62);
}
#time{
    color:rgb(136, 136, 141);
    padding-right: .6em;
}